.elements {

    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, 282px);
    row-gap: 20px;
    column-gap: 17px;
    justify-content: center;
}

@media screen and (max-width: 618px) {
.elements {
  grid-template-columns: 282px;
  justify-content: center;
}
}



