.header {
  max-width: 880px;
  min-height: 74px;
  border-bottom: solid 1px rgba(84, 84, 84, 0.7);
  margin-top: 45px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  padding: 0;
}

@media screen and (max-width: 980px) {
.header {
  width: calc(100% - 80px);
}
}

@media screen and (max-width: 320px) {
.header {
  width: 100%;
  max-width: 320px;
  margin: 0;
  margin-top: 28px;
}
}
