.element__button-trash {
background-image: url(../../../images/Trash.svg);
background-size: 18px 19px;
width: 18px;
height: 19px;
background-color: transparent;
background-repeat: no-repeat;
position: absolute;
border-style: none;
padding: 0;
cursor: pointer;
margin: 0;
margin-left: 244px;
