.profile__aboute {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: calc(100% - 30px);
}

@media screen and (max-width: 352px) {
.profile__aboute {
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  max-width: 282px;
  width: 100%;
}
}
