.popup__input {
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  width: 100%;
  margin: 0;
  margin-top: 13px;
  color: #000000;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 5px;
}

@media screen and (max-width: 364px) {
.popup__input {
  max-width: 238px;
  max-height: 27px;
}
}
