.profile__avatar-container {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-right: 30px;
  position: relative;

}

.profile__avatar-container::after {
  content: '';
  z-index: 1;
  opacity: 0.8;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

}

.profile__avatar-container:hover::after {
  background: rgba(0, 0, 0, 0.8);
  cursor: pointer;
  background-image: url(../../../images/Vector69.png);
  background-repeat: no-repeat;
  background-position: center;
  text-align: right;
  white-space: nowrap;
  animation: ani 0.3s linear forwards;
}

@keyframes ani {
  0% {left: 40px; width: 0;}
  100% {left: 0; width: 120px;}
}
